

'use client';

import * as React from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle, CardDescription, CardFooter } from "@/components/ui/card";
import { Tabs, TabsList, TabsTrigger, TabsContent } from "@/components/ui/tabs";
import { FileText, PlusCircle, Trash2, Edit, Gavel, Users, RefreshCcw, BookCopy, BookOpen, BrainCircuit, Loader2 } from "lucide-react";
import { useToast } from "@/hooks/use-toast";
import { useRouter } from "next/navigation";
import {
  Accordion,
  AccordionContent,
  AccordionItem,
  AccordionTrigger,
} from "@/components/ui/accordion";
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
  AlertDialogTrigger,
} from "@/components/ui/alert-dialog";
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogDescription,
  DialogFooter,
  DialogClose,
} from "@/components/ui/dialog";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Badge } from "@/components/ui/badge";
import { cn } from "@/lib/utils";
import type { User, DebateCase, PracticeRound, Ballot, DebateFormat, SpeechStance, DebateTopic } from "@/lib/types";
import { useAuth } from "@/contexts/auth-context";
import { MultiSelect } from "@/components/ui/multi-select";
import { Separator } from "@/components/ui/separator";
import { FlowSheet } from "@/components/dashboard/flow-sheet";
import { ToggleGroup, ToggleGroupItem } from "@/components/ui/toggle-group";
import { Textarea } from "@/components/ui/textarea";
import { extractNsdaTopics } from "@/ai/flows/extract-nsda-topics-flow";

const DEBATE_CASES_STORAGE_KEY = 'work-session-debate-cases';
const PRACTICE_ROUNDS_STORAGE_KEY = 'work-session-practice-rounds';
const DEBATE_TOPICS_STORAGE_KEY = 'work-session-debate-topics';

const getStanceLabels = (format: DebateFormat): { affirmative: string, negative: string } => {
    switch (format) {
        case 'PF': return { affirmative: 'Pro', negative: 'Con' };
        case 'WSD': return { affirmative: 'Proposition', negative: 'Opposition' };
        default: return { affirmative: 'Affirmative', negative: 'Negative' };
    }
};

export default function DebateEventsPage() {
  const { user, allUsers } = useAuth();
  const { toast } = useToast();
  const router = useRouter();

  const [cases, setCases] = React.useState<DebateCase[]>(() => {
    if (typeof window === 'undefined') return [];
    const stored = localStorage.getItem(DEBATE_CASES_STORAGE_KEY);
    try {
        return stored ? JSON.parse(stored) : [];
    } catch (e) {
        return [];
    }
  });
  
  const [practiceRounds, setPracticeRounds] = React.useState<PracticeRound[]>(() => {
    if (typeof window === 'undefined') return [];
    const stored = localStorage.getItem(PRACTICE_ROUNDS_STORAGE_KEY);
     try {
        return stored ? JSON.parse(stored) : [];
    } catch (e) {
        return [];
    }
  });

  const [topics, setTopics] = React.useState<DebateTopic[]>(() => {
    if (typeof window === 'undefined') return [];
    const stored = localStorage.getItem(DEBATE_TOPICS_STORAGE_KEY);
     try {
        return stored ? JSON.parse(stored) : [];
    } catch (e) {
        return [];
    }
  });

  const [isRoundDialogOpen, setIsRoundDialogOpen] = React.useState(false);
  const [roundToEdit, setRoundToEdit] = React.useState<PracticeRound | null>(null);
  
  const [isTopicDialogOpen, setIsTopicDialogOpen] = React.useState(false);
  const [isImporting, setIsImporting] = React.useState(false);

  React.useEffect(() => {
    localStorage.setItem(DEBATE_CASES_STORAGE_KEY, JSON.stringify(cases));
  }, [cases]);
  
  React.useEffect(() => {
    localStorage.setItem(PRACTICE_ROUNDS_STORAGE_KEY, JSON.stringify(practiceRounds));
  }, [practiceRounds]);
  
  React.useEffect(() => {
    localStorage.setItem(DEBATE_TOPICS_STORAGE_KEY, JSON.stringify(topics));
  }, [topics]);

  const handleCreateCase = (topicId: string, stance: SpeechStance) => {
    if (!user) return;
    const topic = topics.find(t => t.id === topicId);
    if (!topic) {
        toast({ variant: "destructive", title: "Topic not found." });
        return;
    }
    
    // Check if case already exists
    const existingCase = cases.find(c => c.topicId === topicId && c.stance === stance && c.ownerId === user.id);
    if (existingCase) {
        router.push(`/dashboard/debate-events/${existingCase.id}`);
        return;
    }

    const newCase: DebateCase = {
        id: `case-${Date.now()}`,
        ownerId: user.id,
        topicId: topic.id,
        resolution: topic.resolution,
        type: topic.type,
        stance: stance,
        framework: '',
        contentions: [],
        cards: [],
        blocks: [],
    };
    setCases(prev => [newCase, ...prev]);
    toast({ title: "Case Created", description: "Redirecting to the case editor..." });
    router.push(`/dashboard/debate-events/${newCase.id}`);
  };
  
  const handleDeleteCase = (id: string) => {
    setCases(prev => prev.filter(c => c.id !== id));
    toast({ title: "Case Deleted", variant: "destructive" });
  };
  
  const handleSaveRound = (roundData: Omit<PracticeRound, 'id'>) => {
     if (!roundData.topic.trim()) {
        toast({
            variant: 'destructive',
            title: 'Topic Required',
            description: 'Please enter a topic/resolution for the round.',
        });
        return;
    }
     if (roundToEdit) {
      const updatedRound: PracticeRound = { ...roundToEdit, ...roundData };
      setPracticeRounds(prev => prev.map(r => r.id === roundToEdit.id ? updatedRound : r));
      toast({ title: "Practice Round Updated" });
    } else {
      const newRound: PracticeRound = {
        id: `round-${Date.now()}`,
        ...roundData,
      };
      setPracticeRounds(prev => [newRound, ...prev]);
      toast({ title: "Practice Round Created" });
    }
    setIsRoundDialogOpen(false);
    setRoundToEdit(null);
  }
  
  const handleDeleteRound = (id: string) => {
    setPracticeRounds(prev => prev.filter(r => r.id !== id));
    toast({ title: "Practice Round Deleted", variant: "destructive" });
  };
  
  const handleBallotUpdate = (roundId: string, ballot: Ballot) => {
    setPracticeRounds(prev => prev.map(r => r.id === roundId ? { ...r, ballot } : r));
    toast({title: "Ballot Submitted"});
  }

  const handleFlowUpdate = (roundId: string, flow: { aff?: string[][], neg?: string[][] }) => {
     setPracticeRounds(prev => prev.map(r => r.id === roundId ? { ...r, flow: {...r.flow, ...flow} } : r));
  }
  
  const handleCreateTopic = (topic: Omit<DebateTopic, 'id' | 'createdAt'>) => {
    const newTopic: DebateTopic = {
        id: `topic-${Date.now()}`,
        createdAt: new Date().toISOString(),
        ...topic,
    };
    setTopics(prev => [newTopic, ...prev]);
    toast({ title: 'Topic Created' });
    setIsTopicDialogOpen(false);
  }
  
  const handleDeleteTopic = (id: string) => {
    // Also delete associated cases
    setCases(prev => prev.filter(c => c.topicId !== id));
    setTopics(prev => prev.filter(t => t.id !== id));
    toast({ title: "Topic Deleted", description: "The topic and all associated cases have been removed.", variant: "destructive" });
  }

  const handleImportNsdaTopics = async () => {
    setIsImporting(true);
    try {
      const newTopics = await extractNsdaTopics();
      const existingResolutions = new Set(topics.map(t => t.resolution));
      const uniqueNewTopics: DebateTopic[] = [];

      newTopics.forEach(topic => {
        if (!existingResolutions.has(topic.resolution)) {
          uniqueNewTopics.push({
            ...topic,
            id: `topic-${Date.now()}-${uniqueNewTopics.length}`,
            createdAt: new Date().toISOString(),
          });
          existingResolutions.add(topic.resolution);
        }
      });
      
      if (uniqueNewTopics.length > 0) {
        setTopics(prev => [...uniqueNewTopics, ...prev]);
        toast({
          title: "Topics Imported",
          description: `Successfully imported ${uniqueNewTopics.length} new topic(s) from the NSDA.`
        });
      } else {
        toast({
          title: "No New Topics",
          description: "All current NSDA topics are already in your list."
        });
      }
    } catch (error) {
      console.error("Failed to import NSDA topics:", error);
      toast({
        variant: "destructive",
        title: "Import Failed",
        description: (error as Error).message || "Could not fetch topics from the NSDA website."
      });
    } finally {
      setIsImporting(false);
    }
  }

  const userCases = React.useMemo(() => {
    if (!user) return [];
    return cases.filter(c => c.ownerId === user.id);
  }, [cases, user]);

  const topicsWithUserCases = React.useMemo(() => {
    const topicMap = new Map<string, { topic: DebateTopic; cases: { aff?: DebateCase, neg?: DebateCase } }>();
    
    // First, add all topics that have cases
    userCases.forEach(caseItem => {
        const topic = topics.find(t => t.id === caseItem.topicId);
        if (topic) {
            if (!topicMap.has(topic.id)) {
                topicMap.set(topic.id, { topic, cases: {} });
            }
            const entry = topicMap.get(topic.id)!;
            if (caseItem.stance === 'affirmative') {
                entry.cases.aff = caseItem;
            } else {
                entry.cases.neg = caseItem;
            }
        }
    });

    // Then, add all topics, so even ones without cases are available to be created
    topics.forEach(topic => {
      if (!topicMap.has(topic.id)) {
        topicMap.set(topic.id, { topic, cases: {} });
      }
    });


    return Array.from(topicMap.values()).sort((a, b) => new Date(b.topic.createdAt).getTime() - new Date(a.topic.createdAt).getTime());
  }, [topics, userCases]);

  const tabList = [
    { value: 'case-creator', label: 'Case Creator', adminOnly: false },
    { value: 'practice-rounds', label: 'Practice Rounds', adminOnly: false },
    { value: 'topic-management', label: 'Topic Management', adminOnly: true }
  ];

  const visibleTabs = tabList.filter(t => !t.adminOnly || user?.role === 'admin');

  return (
    <div className="flex flex-col gap-6">
      <div>
        <h1 className="text-3xl font-bold font-headline">Debate Events</h1>
        <p className="text-muted-foreground">
          Construct cases and run practice rounds.
        </p>
      </div>

      <Tabs defaultValue="case-creator" className="w-full">
        <TabsList className="grid w-full" style={{ gridTemplateColumns: `repeat(${visibleTabs.length}, 1fr)` }}>
            {visibleTabs.map(tab => (
                <TabsTrigger key={tab.value} value={tab.value}>{tab.label}</TabsTrigger>
            ))}
        </TabsList>
        <TabsContent value="case-creator" className="mt-4 space-y-6">
           <Card>
            <CardHeader>
                <CardTitle>My Cases</CardTitle>
                <CardDescription>Select a topic to start building or editing your cases.</CardDescription>
            </CardHeader>
            <CardContent>
                 <Accordion type="single" collapsible className="w-full">
                    {topicsWithUserCases.map(({ topic, cases }) => {
                        const { affirmative, negative } = getStanceLabels(topic.type);
                        return (
                            <AccordionItem value={topic.id} key={topic.id}>
                                <AccordionTrigger>
                                    <div className="flex flex-col text-left items-start">
                                        <p className="font-semibold">{topic.resolution}</p>
                                        <Badge variant="secondary" className="mt-1">{topic.type}</Badge>
                                    </div>
                                </AccordionTrigger>
                                <AccordionContent className="space-y-4">
                                     <div className="grid sm:grid-cols-2 gap-4">
                                        <CaseSideCard stance="affirmative" label={affirmative} caseData={cases.aff} topicId={topic.id} onCreate={handleCreateCase} onDelete={handleDeleteCase} router={router} />
                                        <CaseSideCard stance="negative" label={negative} caseData={cases.neg} topicId={topic.id} onCreate={handleCreateCase} onDelete={handleDeleteCase} router={router} />
                                     </div>
                                </AccordionContent>
                            </AccordionItem>
                        )
                    })}
                     {topicsWithUserCases.length === 0 && (
                        <div className="text-center text-muted-foreground py-8">No cases created yet. Go to Topic Management to add a topic, then create a case from there.</div>
                     )}
                 </Accordion>
            </CardContent>
          </Card>
        </TabsContent>
        
        <TabsContent value="practice-rounds" className="mt-4 space-y-6">
           <Card>
            <CardHeader>
              <div className="flex items-center justify-between">
                <CardTitle>My Practice Rounds</CardTitle>
                <Button onClick={() => { setRoundToEdit(null); setIsRoundDialogOpen(true); }}>
                  <PlusCircle className="mr-2" /> New Round
                </Button>
              </div>
              <CardDescription>Manage and participate in practice rounds.</CardDescription>
            </CardHeader>
            <CardContent>
               {practiceRounds.length > 0 ? (
                <Accordion type="single" collapsible className="w-full">
                  {practiceRounds.map((round) => (
                    <PracticeRoundItem key={round.id} round={round} onBallotUpdate={handleBallotUpdate} onFlowUpdate={handleFlowUpdate} allUsers={allUsers} onDelete={handleDeleteRound} onEdit={() => { setRoundToEdit(round); setIsRoundDialogOpen(true); }} />
                  ))}
                </Accordion>
              ) : (
                <div className="text-center text-muted-foreground py-8">No practice rounds created yet.</div>
              )}
            </CardContent>
          </Card>
        </TabsContent>

        {user?.role === 'admin' && (
            <TabsContent value="topic-management" className="mt-4 space-y-6">
            <Card>
                <CardHeader>
                <div className="flex justify-between items-start">
                    <div>
                        <CardTitle>Topic Management</CardTitle>
                        <CardDescription>Add, import, and manage debate topics for the team.</CardDescription>
                    </div>
                    <div className="flex gap-4">
                        <Button onClick={() => setIsTopicDialogOpen(true)}><PlusCircle className="mr-2"/> Add New Topic</Button>
                        <Button variant="outline" onClick={handleImportNsdaTopics} disabled={isImporting}>
                            {isImporting ? <Loader2 className="mr-2 animate-spin"/> : <BookCopy className="mr-2"/>}
                            {isImporting ? 'Importing...' : 'Import NSDA Topics'}
                        </Button>
                    </div>
                </div>
                </CardHeader>
                <CardContent>
                    {topics.length > 0 ? (
                        <ul className="space-y-2">
                            {topics.map(topic => (
                                <li key={topic.id} className="flex items-center justify-between p-3 rounded-md border bg-muted/50">
                                    <div>
                                        <p className="font-medium">{topic.resolution}</p>
                                        <Badge variant="secondary">{topic.type}</Badge>
                                    </div>
                                    <AlertDialog>
                                        <AlertDialogTrigger asChild>
                                            <Button size="icon" variant="destructive"><Trash2 className="h-4 w-4"/></Button>
                                        </AlertDialogTrigger>
                                        <AlertDialogContent>
                                            <AlertDialogHeader>
                                                <AlertDialogTitle>Delete Topic?</AlertDialogTitle>
                                                <AlertDialogDescription>This will permanently delete the topic and ALL associated cases. This action cannot be undone.</AlertDialogDescription>
                                            </AlertDialogHeader>
                                            <AlertDialogFooter>
                                                <AlertDialogCancel>Cancel</AlertDialogCancel>
                                                <AlertDialogAction onClick={() => handleDeleteTopic(topic.id)}>Delete</AlertDialogAction>
                                            </AlertDialogFooter>
                                        </AlertDialogContent>
                                    </AlertDialog>
                                </li>
                            ))}
                        </ul>
                    ) : (
                        <div className="text-center text-muted-foreground py-8">No topics created yet.</div>
                    )}
                </CardContent>
            </Card>
            </TabsContent>
        )}
      </Tabs>
      
      <RoundDialog
        isOpen={isRoundDialogOpen}
        onOpenChange={setIsRoundDialogOpen}
        onSave={handleSaveRound}
        roundToEdit={roundToEdit}
        allUsers={allUsers}
       />
       <TopicDialog
        isOpen={isTopicDialogOpen}
        onOpenChange={setIsTopicDialogOpen}
        onCreate={handleCreateTopic}
       />
    </div>
  );
}


function RoundDialog({ isOpen, onOpenChange, onSave, roundToEdit, allUsers }: { isOpen: boolean, onOpenChange: (open: boolean) => void, onSave: (data: Omit<PracticeRound, 'id'>) => void, roundToEdit: PracticeRound | null, allUsers: User[] }) {
    const [topic, setTopic] = React.useState('');
    const [type, setType] = React.useState<DebateFormat>('LD');
    const [judges, setJudges] = React.useState<string[]>([]);
    const [affTeam, setAffTeam] = React.useState<string[]>([]);
    const [negTeam, setNegTeam] = React.useState<string[]>([]);
    const { toast } = useToast();

    const availableUsers = React.useMemo(() => {
        return allUsers.map(u => ({label: u.name, value: u.id}));
    }, [allUsers]);

    const maxTeamSize = React.useMemo(() => {
        switch (type) {
            case 'PF':
            case 'CX':
                return 2;
            case 'WSD':
                return 5;
            case 'LD':
            default:
                return 1;
        }
    }, [type]);

    React.useEffect(() => {
        if (roundToEdit) {
            setTopic(roundToEdit.topic);
            setType(roundToEdit.type);
            setJudges(roundToEdit.judges);
            setAffTeam(roundToEdit.affTeam || []);
            setNegTeam(roundToEdit.negTeam || []);
        } else {
            setTopic('');
            setType('LD');
            setJudges([]);
            setAffTeam([]);
            setNegTeam([]);
        }
    }, [roundToEdit, isOpen]);
    
    React.useEffect(() => {
        // Reset teams if they exceed max size for the new format
        if (affTeam.length > maxTeamSize) setAffTeam(affTeam.slice(0, maxTeamSize));
        if (negTeam.length > maxTeamSize) setNegTeam(negTeam.slice(0, maxTeamSize));
    }, [type, maxTeamSize, affTeam, negTeam]);

    const handleSave = () => {
        if (!topic.trim()) {
            toast({
                variant: 'destructive',
                title: 'Topic Required',
                description: 'Please enter a topic/resolution for the round.',
            });
            return;
        }
        const initialFlow = { aff: Array(4).fill(Array(4).fill('')), neg: Array(4).fill(Array(4).fill('')) };
        const allParticipants = [...new Set([...affTeam, ...negTeam, ...judges])];
        onSave({ topic, type, participants: allParticipants, judges, ballot: roundToEdit?.ballot, flow: roundToEdit?.flow || initialFlow, affTeam, negTeam });
    };

    const handleFlip = () => {
        const currentAff = [...affTeam];
        const currentNeg = [...negTeam];
        setAffTeam(currentNeg);
        setNegTeam(currentAff);
    }
    
    const TeamSelect = ({ label, value, onChange, max, disabledIds }: { label: string, value: string[], onChange: (value: string[]) => void, max: number, disabledIds: string[] }) => {
        const options = availableUsers.filter(u => !disabledIds.includes(u.value) || value.includes(u.value));
        if (max === 1) {
            return (
                 <Select value={value[0] || ''} onValueChange={(v) => onChange(v ? [v] : [])}>
                    <SelectTrigger><SelectValue placeholder={`Select ${label}`} /></SelectTrigger>
                    <SelectContent>
                        {options.map(p => (
                            <SelectItem key={p.value} value={p.value}>{p.label}</SelectItem>
                        ))}
                    </SelectContent>
                </Select>
            )
        }
        return (
            <MultiSelect options={options} value={value} onValueChange={onChange} placeholder={`Select ${label} (up to ${max})...`} maxCount={max} />
        )
    }

    return (
        <Dialog open={isOpen} onOpenChange={onOpenChange}>
            <DialogContent>
                <DialogHeader>
                    <DialogTitle>{roundToEdit ? 'Edit' : 'Create'} Practice Round</DialogTitle>
                </DialogHeader>
                <div className="space-y-4 py-4">
                    <div className="space-y-2">
                        <Label htmlFor="round-topic">Topic/Resolution</Label>
                        <Input id="round-topic" value={topic} onChange={e => setTopic(e.target.value)} />
                    </div>
                    <div className="space-y-2">
                        <Label htmlFor="round-type">Debate Type</Label>
                         <Select value={type} onValueChange={(v: DebateFormat) => setType(v)}>
                            <SelectTrigger id="round-type"><SelectValue/></SelectTrigger>
                            <SelectContent>
                                <SelectItem value="LD">Lincoln Douglas (LD)</SelectItem>
                                <SelectItem value="PF">Public Forum (PF)</SelectItem>
                                <SelectItem value="CX">Cross Examination (CX)</SelectItem>
                                <SelectItem value="WSD">World Schools Debate (WSD)</SelectItem>
                            </SelectContent>
                        </Select>
                    </div>

                    <div className="flex items-center gap-2">
                         <div className="flex-1 space-y-2">
                            <Label>Affirmative</Label>
                            <TeamSelect label="Affirmative" value={affTeam} onChange={setAffTeam} max={maxTeamSize} disabledIds={negTeam} />
                        </div>
                        <Button size="icon" variant="outline" className="mt-8 flex-shrink-0" onClick={handleFlip}><RefreshCcw className="h-4 w-4"/></Button>
                         <div className="flex-1 space-y-2">
                            <Label>Negative</Label>
                            <TeamSelect label="Negative" value={negTeam} onChange={setNegTeam} max={maxTeamSize} disabledIds={affTeam} />
                        </div>
                    </div>
                    
                     <div className="space-y-2">
                        <Label>Judges</Label>
                        <MultiSelect options={availableUsers} value={judges} onValueChange={setJudges} placeholder="Select judge(s)..."/>
                    </div>
                </div>
                <DialogFooter>
                    <DialogClose asChild><Button variant="outline">Cancel</Button></DialogClose>
                    <Button onClick={handleSave}>Save Round</Button>
                </DialogFooter>
            </DialogContent>
        </Dialog>
    );
}

function PracticeRoundItem({ round, onBallotUpdate, onFlowUpdate, allUsers, onDelete, onEdit }: { round: PracticeRound, onBallotUpdate: (id: string, ballot: Ballot) => void, onFlowUpdate: (id: string, flow: { aff?: string[][], neg?: string[][] }) => void, allUsers: User[], onDelete: (id: string) => void, onEdit: () => void }) {
    const { user } = useAuth();
    const isJudge = user ? round.judges.includes(user.id) : false;
    
    const affTeam = (round.affTeam || []).map(id => allUsers.find(u => u.id === id)?.name).filter(Boolean);
    const negTeam = (round.negTeam || []).map(id => allUsers.find(u => u.id === id)?.name).filter(Boolean);
    
    return (
        <AccordionItem value={round.id}>
            <AccordionTrigger>
                <div className="flex-1 text-left">
                    {round.topic}
                    <Badge variant="secondary" className="ml-2">{round.type}</Badge>
                </div>
            </AccordionTrigger>
            <AccordionContent className="space-y-4">
                <div className="grid md:grid-cols-2 gap-4 text-sm">
                    <div>
                        <h4 className="font-semibold flex items-center gap-2 mb-1"><Users className="h-4 w-4"/> Sides</h4>
                        <div className="flex flex-col gap-1 text-muted-foreground">
                            <span><Badge>AFF</Badge> {affTeam.join(', ') || 'Not set'}</span>
                            <span><Badge variant="destructive">NEG</Badge> {negTeam.join(', ') || 'Not set'}</span>
                        </div>
                    </div>
                     <div className="space-y-1">
                        <h4 className="font-semibold flex items-center gap-2"><Gavel className="h-4 w-4"/> Judges</h4>
                        <p className="text-muted-foreground">{round.judges.map(pId => allUsers.find(u => u.id === pId)?.name).join(', ')}</p>
                    </div>
                </div>
                 <div className="flex justify-between items-center gap-2">
                    <div className="flex gap-2">
                        <Button size="sm" variant="outline" onClick={onEdit}><Edit className="mr-2"/> Edit</Button>
                        <AlertDialog>
                            <AlertDialogTrigger asChild><Button size="sm" variant="destructive"><Trash2 className="mr-2"/> Delete</Button></AlertDialogTrigger>
                            <AlertDialogContent>
                                <AlertDialogHeader><AlertDialogTitle>Delete this round?</AlertDialogTitle></AlertDialogHeader>
                                <AlertDialogFooter>
                                    <AlertDialogCancel>Cancel</AlertDialogCancel>
                                    <AlertDialogAction onClick={() => onDelete(round.id)}>Delete</AlertDialogAction>
                                </AlertDialogFooter>
                            </AlertDialogContent>
                        </AlertDialog>
                    </div>
                 </div>
                 <Separator />
                 <Accordion type="single" collapsible className="w-full" defaultValue="ballot">
                    {round.type === "LD" ? (
                        <>
                             <AccordionItem value="aff-flow">
                                <AccordionTrigger>Aff Flow</AccordionTrigger>
                                <AccordionContent>
                                <FlowSheet flow={round.flow?.aff} onUpdate={(newFlow) => onFlowUpdate(round.id, { aff: newFlow })} />
                                </AccordionContent>
                            </AccordionItem>
                             <AccordionItem value="neg-flow">
                                <AccordionTrigger>Neg Flow</AccordionTrigger>
                                <AccordionContent>
                                <FlowSheet flow={round.flow?.neg} onUpdate={(newFlow) => onFlowUpdate(round.id, { neg: newFlow })} />
                                </AccordionContent>
                            </AccordionItem>
                        </>
                    ) : (
                         <AccordionItem value="flow">
                            <AccordionTrigger>Flow</AccordionTrigger>
                            <AccordionContent>
                               <FlowSheet flow={round.flow?.aff} onUpdate={(newFlow) => onFlowUpdate(round.id, { aff: newFlow })} />
                            </AccordionContent>
                        </AccordionItem>
                    )}
                    <AccordionItem value="ballot">
                        <AccordionTrigger>Ballot</AccordionTrigger>
                        <AccordionContent>
                            <BallotForm ballot={round.ballot} isJudge={isJudge} onUpdate={(newBallot) => onBallotUpdate(round.id, newBallot)} />
                        </AccordionContent>
                    </AccordionItem>
                </Accordion>
            </AccordionContent>
        </AccordionItem>
    );
}

function BallotForm({ ballot, isJudge, onUpdate }: { ballot?: Ballot, isJudge: boolean, onUpdate: (ballot: Ballot) => void }) {
    const [winner, setWinner] = React.useState(ballot?.winner || '');
    const [rfd, setRfd] = React.useState(ballot?.rfd || '');
    const [affPoints, setAffPoints] = React.useState(ballot?.affPoints || '');
    const [negPoints, setNegPoints] = React.useState(ballot?.negPoints || '');
    const [affComments, setAffComments] = React.useState(ballot?.affComments || '');
    const [negComments, setNegComments] = React.useState(ballot?.negComments || '');
    
    const handleSave = () => {
        onUpdate({ winner, rfd, affPoints, negPoints, affComments, negComments });
    }

    if (!isJudge && !ballot) {
        return <p className="text-sm text-muted-foreground text-center py-4">The ballot has not been filled out yet.</p>;
    }
    
    return (
        <div className="space-y-4">
            <div className="grid md:grid-cols-3 gap-4">
                <div className="space-y-2">
                    <Label>Winner</Label>
                    <Input value={winner} onChange={e => setWinner(e.target.value)} readOnly={!isJudge} />
                </div>
                 <div className="space-y-2">
                    <Label>Aff Speaker Points</Label>
                    <Input value={affPoints} onChange={e => setAffPoints(e.target.value)} readOnly={!isJudge} />
                </div>
                 <div className="space-y-2">
                    <Label>Neg Speaker Points</Label>
                    <Input value={negPoints} onChange={e => setNegPoints(e.target.value)} readOnly={!isJudge} />
                </div>
            </div>
             <div className="space-y-2">
                <Label>Reason for Decision (RFD)</Label>
                <Textarea value={rfd} onChange={e => setRfd(e.target.value)} readOnly={!isJudge} />
            </div>
            <div className="grid md:grid-cols-2 gap-4">
                <div className="space-y-2">
                    <Label>Comments for Affirmative</Label>
                    <Textarea value={affComments} onChange={e => setAffComments(e.target.value)} readOnly={!isJudge} />
                </div>
                <div className="space-y-2">
                    <Label>Comments for Negative</Label>
                    <Textarea value={negComments} onChange={e => setNegComments(e.target.value)} readOnly={!isJudge} />
                </div>
            </div>
            {isJudge && <Button onClick={handleSave}>Submit Ballot</Button>}
        </div>
    );
}

function TopicDialog({ isOpen, onOpenChange, onCreate }: { isOpen: boolean, onOpenChange: (open: boolean) => void, onCreate: (topic: Omit<DebateTopic, 'id' | 'createdAt'>) => void }) {
    const [resolution, setResolution] = React.useState('');
    const [type, setType] = React.useState<DebateFormat | ''>('');
    const { toast } = useToast();

    const handleCreate = () => {
        if (!resolution.trim() || !type) {
            toast({ variant: 'destructive', title: 'Missing Fields', description: 'Please provide a resolution and debate type.' });
            return;
        }
        onCreate({ resolution, type });
        setResolution('');
        setType('');
    };

    return (
        <Dialog open={isOpen} onOpenChange={onOpenChange}>
            <DialogContent>
                <DialogHeader>
                    <DialogTitle>Add New Topic</DialogTitle>
                    <DialogDescription>Create a new resolution for the team to use.</DialogDescription>
                </DialogHeader>
                <div className="space-y-4 py-4">
                    <div className="space-y-2">
                        <Label htmlFor="topic-resolution">Resolution</Label>
                        <Textarea id="topic-resolution" value={resolution} onChange={e => setResolution(e.target.value)} placeholder="Resolved: ..." />
                    </div>
                    <div className="space-y-2">
                        <Label htmlFor="topic-type">Debate Type</Label>
                        <Select value={type} onValueChange={(v: DebateFormat) => setType(v)}>
                            <SelectTrigger id="topic-type"><SelectValue placeholder="Select a format..." /></SelectTrigger>
                            <SelectContent>
                                <SelectItem value="LD">Lincoln Douglas (LD)</SelectItem>
                                <SelectItem value="PF">Public Forum (PF)</SelectItem>
                                <SelectItem value="CX">Cross Examination (CX)</SelectItem>
                                <SelectItem value="WSD">World Schools Debate (WSD)</SelectItem>
                            </SelectContent>
                        </Select>
                    </div>
                </div>
                <DialogFooter>
                    <DialogClose asChild><Button variant="outline">Cancel</Button></DialogClose>
                    <Button onClick={handleCreate}>Create Topic</Button>
                </DialogFooter>
            </DialogContent>
        </Dialog>
    );
}

function CaseSideCard({ stance, label, caseData, topicId, onCreate, onDelete, router }: { stance: SpeechStance, label: string, caseData: DebateCase | undefined, topicId: string, onCreate: (topicId: string, stance: SpeechStance) => void, onDelete: (caseId: string) => void, router: any }) {
    if (caseData) {
        return (
            <Card>
                <CardHeader>
                    <CardTitle className="text-lg">{label}</CardTitle>
                    <CardDescription>Case created. You can now edit it.</CardDescription>
                </CardHeader>
                <CardContent className="space-y-2">
                    <p className="font-semibold text-sm">Framework Preview:</p>
                    <p className="text-sm text-muted-foreground line-clamp-2">{caseData.framework || "No framework written."}</p>
                </CardContent>
                <CardFooter className="flex gap-2">
                    <Button size="sm" className="flex-1" onClick={() => router.push(`/dashboard/debate-events/${caseData.id}`)}>
                        <Edit className="mr-2 h-4 w-4" /> Edit Case
                    </Button>
                    <AlertDialog>
                        <AlertDialogTrigger asChild><Button size="icon" variant="destructive"><Trash2 className="h-4 w-4" /></Button></AlertDialogTrigger>
                        <AlertDialogContent>
                            <AlertDialogHeader><AlertDialogTitle>Delete this case?</AlertDialogTitle><AlertDialogDescription>This action cannot be undone.</AlertDialogDescription></AlertDialogHeader>
                            <AlertDialogFooter>
                                <AlertDialogCancel>Cancel</AlertDialogCancel>
                                <AlertDialogAction onClick={() => onDelete(caseData.id)}>Delete</AlertDialogAction>
                            </AlertDialogFooter>
                        </AlertDialogContent>
                    </AlertDialog>
                </CardFooter>
            </Card>
        );
    }

    return (
        <Card className="border-dashed">
            <CardHeader>
                <CardTitle className="text-lg">{label}</CardTitle>
                <CardDescription>No case created for this side yet.</CardDescription>
            </CardHeader>
            <CardContent>
                <div className="flex items-center justify-center h-24">
                     <Button variant="secondary" onClick={() => onCreate(topicId, stance)}>
                        <PlusCircle className="mr-2" /> Create {label} Case
                    </Button>
                </div>
            </CardContent>
        </Card>
    );
}

    

    
